.logo {
  display: flex;
  border-bottom: 1px solid rgba(255, 255, 255, 0);
  color: inherit;
}
@media (hover: hover) {
  .logo:hover:not(:active) {
    border-bottom: 1px solid;
  }
}
.logo.focus-visible:focus:not(:active) {
  outline: 1px solid;
  border-bottom: 1px solid rgba(255, 255, 255, 0);
}
.logo:active {
  outline: none;
}

@media (max-width: 1199px) {
  .logo--footer {
    padding-top: 10px;
  }
}

@media (max-width: 1199px) {
  .logo svg {
    width: 144px;
    height: 44px;
  }
}
@media (max-width: 767px) {
  .logo svg {
    width: 112px;
    height: 34px;
  }
}

@media (max-width: 1199px) {
  .logo--footer svg {
    width: 118px;
    height: 34px;
  }
}

.btn {
  display: inline-flex;
  justify-content: center;
  user-select: none;
  color: #ffffff;
  background-color: #0266c1;
  border-width: 1px;
  border-style: solid;
  border-color: #0266c1;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(:active):not(.btn--disabled) {
    border-color: #1c3374;
    background-color: rgba(255, 255, 255, 0);
    color: #1c3374;
  }
}
.btn:focus:not(.btn--disabled) {
  border-color: #1c3374;
  background-color: rgba(255, 255, 255, 0);
  color: #1c3374;
}
.btn:active:not(.btn--disabled) {
  border-color: #1c3374;
  background-color: #1c3374;
  color: #ffffff;
}
.btn--disabled {
  color: #ffffff;
  background-color: #0266c1;
  opacity: 0.1;
  cursor: default;
}

.contact-phone {
  color: inherit;
  font-weight: 600;
  font-size: 18px;
  line-height: 1em;
}
@media (hover: hover) {
  .contact-phone:hover:not(:active) {
    border-bottom: 1px solid;
  }
}
.contact-phone.focus-visible:focus:not(:active) {
  outline: 1px solid;
  border-bottom: 0;
}
@media (max-width: 1199px) {
  .contact-phone {
    font-size: 16px;
  }
}
@media (max-width: 767px) {
  .contact-phone {
    font-size: 14px;
  }
}

.privacy-policy {
  max-width: 220px;
  color: #2d383f;
  font-size: 12px;
  line-height: 1em;
  font-weight: 300;
  border-bottom: 1px solid transparent;
}
@media (hover: hover) {
  .privacy-policy:hover:not(:active):not(.privacy-policy--disabled) {
    border-bottom: 1px solid;
  }
}
.privacy-policy.focus-visible:focus:not(:active):not(.privacy-policy--disabled) {
  outline: 1px solid;
  border-bottom: 0;
}
.privacy-policy:active:not(.privacy-policy--disabled) {
  outline: none;
  border-bottom: 0;
}
.privacy-policy--disabled {
  cursor: default;
  opacity: 0.5;
  outline: none;
}

.video {
  width: 482px;
  height: 317px;
  user-select: none;
}
@media (max-width: 1199px) {
  .video {
    display: flex;
    justify-content: center;
    width: 65%;
    height: auto;
  }
}
@media (width: 768px) {
  .video {
    height: 300px;
  }
}
@media (max-width: 767px) {
  .video {
    display: none;
  }
}

.video__play-button {
  position: absolute;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 55px;
  height: 55px;
  top: calc(50% - 27px);
  left: calc(50% - 27px);
  cursor: pointer;
  color: #ffffff;
  border-width: 1px;
  border-style: solid;
  border-color: #ffffff;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.2);
}
@media (hover: hover) {
  .video__play-button:hover:not(:active):not(:disabled) {
    background: rgba(255, 255, 255, 0);
    border-color: #1c3374;
  }
  .video__play-button:hover:not(:active):not(:disabled) svg:last-child {
    display: block;
  }
}
.video__play-button:focus:not(:disabled) {
  background: rgba(255, 255, 255, 0);
  border-color: #1c3374;
}
.video__play-button:focus:not(:disabled) svg:last-child {
  display: block;
}
.video__play-button:active:not(:disabled) {
  background: #1c3374;
  border-color: #1c3374;
}
.video__play-button:disabled {
  background: rgba(255, 255, 255, 0);
  border-color: #0266c1;
  color: #0266c1;
  opacity: 0.1;
  cursor: default;
}

.video__play-button svg {
  position: relative;
  z-index: 2;
  margin-left: 5px;
}

.video__play-button svg:last-child {
  position: absolute;
  display: none;
  z-index: 1;
  color: #000000;
  top: 13.7px;
  left: 15.4px;
}

.slide-cards {
  user-select: none;
}
@media (max-width: 767px) {
  .slide-cards {
    display: none;
  }
}

.slide-cards img {
  position: relative;
}

.slide-cards img:first-child {
  right: -175px;
}
@media (max-width: 1199px) {
  .slide-cards img:first-child {
    top: -16px;
    right: auto;
    left: 31px;
    width: 266px;
    height: 351px;
  }
}

.slide-cards img:nth-child(2) {
  height: 366px;
  top: 93px;
}
@media (max-width: 1199px) {
  .slide-cards img:nth-child(2) {
    width: 239px;
    height: 311px;
    top: 38px;
    left: -30px;
  }
}

.audio-frame {
  width: 340px;
  height: 220px;
  display: flex;
}
@media (max-width: 767px) {
  .audio-frame {
    display: none;
  }
}

