.main-header {
  position: relative;

  background-color: $color-additional;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url("../img/jpg/caucasus-mountains-tablet.jpg");

  &:has(.hero__slide--caucasus.swiper-slide-active) {
    background-image: url("../img/jpg/caucasus-mountains.jpg");

    @include vp-1199 {
      background-image: url("../img/jpg/caucasus-mountains-tablet.jpg");
      background-position: 50% 0;
    }
  }

  &:has(.hero__slide--kamchatka.swiper-slide-active) {
    background-image: url("../img/jpg/kamchatka-mountains.jpg");

    @include vp-1199 {
      background-position: 50% 0;
    }
  }

  &:has(.hero__slide--altai.swiper-slide-active) {
    background-image: url("../img/jpg/altai.jpg");

    @include vp-1199 {
      background-image: url("../img/jpg/altai-tablet.jpg");
    }
  }

  transition: background-image $trans-default;

  @include vp-1199 {
    padding-top: 0;

    &:has(.main-nav--opened) {
      .main-header__opened-menu-overlay {
        display: flex;
      }
    }

    &:has(.main-nav--nojs) {
      .main-header__opened-menu-overlay {
        display: flex;
      }
    }
  }
}

.main-header__overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  user-select: none;

  background: linear-gradient(112.1deg, rgba(15, 20, 41, 0.33) 17.23%, rgba(15, 20, 41, 0) 94.93%);
}

.main-header__opened-menu-overlay {
  display: none;
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 100%;
  pointer-events: none;

  background-color: transparentize($color-black, 0.5);
}

.main-header__row {
  display: flex;
  justify-content: space-between;
  position: relative;
  z-index: 3;
  margin-top: 0;
  padding-top: 40px;

  color: $color-default-white;

  @include vp-1199 {
    padding-top: 0;

    &:has(.main-nav--opened) {
      background-color: $color-default-white;
      color: $color-default-dark;
    }

    &:has(.main-nav--nojs) {
      background-color: $color-default-white;
      color: $color-default-dark;
    }
  }
}

.main-header__nav {
  margin-right: auto;
  margin-left: auto;
}

.main-header__logo {
  @include vp-1199 {
    margin-top: 0;
    margin-right: auto;
    margin-bottom: 0;
    margin-left: 35.5%;
  }

  // @include only-768 {
  //   margin-right: 142px;
  // }

  @include vp-767 {
    margin-right: 0;
    margin-left: auto;
  }
}

.main-header__content {
  position: relative;
  z-index: 2;
}
